import { createStore } from 'vuex';
import VuexPersistence from 'vuex-persist';
import applications from './applications.js';

export const store = createStore({
  state: {
    powerOn: false,
    bootup: false,
    showStartMenu: false,
  },
  mutations: {
    setPowerMutation(state, newState) {
      state.powerOn = newState;
    },
    setBootupMutation(state) {
      state.bootup = !state.bootup;
    },
    setStartMenuMutation(state) {
      state.showStartMenu = !state.showStartMenu;
    },
  },
  actions: {
    toggleStartMenu(context) {
      context.commit('setStartMenuMutation');
    },
    togglePower(context) {
      const currentState = context.state.powerOn;
      if (currentState) {
        context.commit('setPowerMutation', false);
      } else {
        context.commit('setPowerMutation', true);
        context.dispatch('toggleBootup');
      }
    },
    toggleBootup(context) {
      context.commit('setBootupMutation');
    },
  },
  modules: {
    applications,
  },
  plugins: [new VuexPersistence().plugin],
});
